<template>
  <div ref="timehart" style="width: 800px;height:550px;">

  </div>
</template>

<script>
import * as echarts from "echarts"
export default {
  name: 'timeChaets',
  mounted(){
},

  props:['timeType','title'],
  



  data () {
    return {

     

      dataResult:[],
      bases:[],
      dateList:[],
      timeList:[],
      dateArr:[],
      soure1:[                                                           //echart soure数据
        // ['task', '2021-10-22', '2021-10-23', '2021-10-24', '2021-10-25'],
        // ['厦门', 56.5, 82.1,35,66],
        // ['福州', 51.1, 51.4,23,77],
        // ['长沙', 40.1, 62.2,45,88],
        // ['杭州', 22, 33,44,99],
        // ['泉州', 25.2, 37.1,56,78],
      ],

      value1:'',                                                           
      series1:[],                                                         //echart series1数据
      baseNum:8,
    }
  },

  methods:{

    setEcharts(){
      
    let timeChaets= echarts.init(this.$refs.timehart)    //初始化echarts
    timeChaets.setOption({                                //数据填充

      legend: {},
      tooltip: {
        trigger: 'axis',
        showContent: false
      },
      title: {
          left: 'left',
          text: this.title
        },
      dataset: {
        source: this.soure1                           //填充source
      },
      xAxis: { type: 'category' },
      yAxis: { gridIndex: 0 },
      grid: { top: '55%' },
      series: this.series1                             // 填充series


      })


    timeChaets.on('updateAxisPointer', function (event) {  //echarts关联联动函数
    const xAxisInfo = event.axesInfo[0];
    if (xAxisInfo) {
      const dimension = xAxisInfo.value + 1;
      timeChaets.setOption({
        series: {
          id: 'pie',
          label: {
            formatter: '{b}: {@[' + dimension + ']} ({d}%)'
          },
          encode: {
            value: dimension,
            tooltip: dimension
          }
        }
      });
    }
  });

    },



    
    series2(){
      let series=[]
      
      for(var i=0;i<this.baseNum;i++){                                //折线图填充
        series.push(
          {
          type: 'line',
          smooth: true,
          seriesLayoutBy: 'row',
          emphasis: { focus: 'series' }
        })
      };
      series.push(                                         //扇形图填充
        {
          type: 'pie',
          id: 'pie',
          radius: ['15%', '30%'],
          center: ['50%', '25%'],
          itemStyle: {
          borderRadius: 5,
          borderColor: '#fff',
          borderWidth: 2
        },

          emphasis: {
            focus: 'self'
          },
          label: {
            formatter: '{b}: {@this.value1} ({d}%)'        //value1填充                          
          },
          encode: {
            itemName: 'task',
            value: this.value1,
            tooltip: this.value1
          }
        });    



      return series
    },



    showData(){
        this.$http.get("/findDataResult").then(res=>{        
        this.dataResult=res.data.results;
    

        this.bases=res.data.bases;
        this.datamation();
        this.setEcharts();
        
        
        }) 


      },

    datamation(){

        
        //日期列表处理
        let dateLi=[]
        for(let i=0;i<this.dataResult.length;i++){   
          dateLi.push(this.dataResult[i]["date"])          
        };
        dateLi=Array.from(new Set(dateLi));
        
        dateLi=dateLi.sort();
        
        
        //工时列表处理
        
        let taskList=[];
        for(let i=0;i<this.bases.length;i++){          
          taskList[i]=[];          
          taskList[i]=[this.bases[i]["base"]] 
          var b=taskList[i]       
          for(let j=0;j<this.dataResult.length;j++){           
            if(this.dataResult[j]["baseId"]==this.bases[i]["id"]){  
              for(let k=0;k<dateLi.length;k++){                 
                if(this.dataResult[j]["date"]==dateLi[k]){
                  b.push(this.dataResult[j][this.timeType])                    
                }  
                              
              }             

            } 
            


          }

          taskList[i]=b         
        
        }

        

            
        dateLi.unshift('task') 

        let c=[]
        c.push(dateLi)
        
        for(let i=0;i<taskList.length;i++){
          c.push(taskList[i])
        }       
        this.soure1= JSON.parse(JSON.stringify(c))
        
        this.value1=this.soure1[0][1]                           //value1赋值
        
        this.series1=this.series2() 
        
        this.baseNum =8   
      }, 















  },

  created(){


    this.showData()
    
   
    // this.value1=this.soure1[0][1]                           //value1赋值
    // this.series1=this.series2()                          //series2赋值

  },
}
</script>

