import Vue from 'vue'
import Router from 'vue-router'
import setTimeLimit from '@/components/setTimeLimit'
import setTroubleShootingTime from '@/components/setTroubleShootingTime'
import upLoad from '@/components/upLoad'
import timeChaets from '@/components/timeChaets'
import timeShow from '@/components/timeShow'
import taskShow from '@/components/taskShow'
import allShow from '@/components/allShow'
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/setTimeLimit', name: 'setTimeLimit',component: setTimeLimit},
    {path: '/setTroubleShootingTime', name: 'setTroubleShootingTime',component: setTroubleShootingTime},
    {path: '/upLoad', name: 'upLoad',component: upLoad},
    {path: '/timeChaets', name: 'timeChaets',component: timeChaets},
    {path: '/timeShow', name: 'timeShow',component: timeShow},
    {path: '/taskShow', name: 'taskShow',component: taskShow},
    {path: '/allShow', name: 'allShow',component: allShow},

  ]
})



